body {
  background-color: #000;
}

p {
  color: #75d4fc;
  font-family: lores-12, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
}

h5, h6 {
  color: #fff;
  display: inline;
}

hr {
  color: #ff000b1a;
  margin-top: 0;
}

.rectangle {
  height: 22px;
  width: 10px;
  opacity: 1;
  background-color: #75d4fc;
  margin-top: 20px;
  animation: fade 1.2s infinite;
}

@keyframes fade {
  0%, 50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

#centerContent {
  width: 550px;
  text-shadow: 1px 1px #67cefa84;
  margin-top: 15vh;
  margin-left: 15vw;
}

#subContent {
  text-align: center;
}

button {
  letter-spacing: 3px;
  text-shadow: -1.5px 0 #ff000b33, -1px -1px 1px #ff000be6;
  background-color: #000;
  border: none;
  margin-top: 60px;
  font-family: americane, sans-serif;
  font-size: 23px;
  font-style: normal;
  font-weight: 900;
}

button:hover {
  opacity: .8;
}

#space1 {
  margin-top: 30px;
}

#space2 {
  margin-top: 50px;
}

#disclaimer {
  color: #fff;
  text-align: center;
  margin-top: 70px;
  margin-bottom: 20px;
  font-family: lores-12, sans-serif;
  font-size: 7px;
  font-style: normal;
  font-weight: 400;
}

#repo {
  margin-left: 50px;
}

@media (min-width: 1070px) {
  .menu-wrap {
    display: none;
  }

  .menu-wrap2 {
    margin-left: 70vw;
  }

  #divideTab {
    cursor: default;
    padding-left: 2em;
    padding-right: 2em;
    font-size: .1em;
  }

  li {
    text-shadow: 0 1px 2px #ededed;
    text-align: center;
    color: #1c1c1c;
    letter-spacing: 4px;
    padding: 10px;
    font-family: jubilat-thin, serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 1px;
    position: relative;
  }
}

@media (max-width: 1070px) {
  #homeIcon {
    position: relative;
  }

  .menu-wrap2, .m2 {
    visibility: hidden;
    display: none;
  }

  .menuThin {
    visibility: hidden;
    font-size: 0;
    display: none;
  }

  #menuLinks2 {
    visibility: hidden;
    font-size: 100px;
  }

  .menu-wrap {
    margin-right: 5vw;
  }

  .m2 {
    font-size: 300px;
  }

  li {
    padding-left: 3em;
    padding-right: 3em;
  }

  #menuLinks {
    font-size: 17px;
  }
}

